import { db } from "@/lib/db"
import { getAuthUser } from "@/lib/auth-utils"
import { NextResponse } from "next/server"

export async function GET() {
  try {
    const authUser = await getAuthUser()

    if (!authUser) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 })
    }

    // Гарантируем существование пользователя и профиля, чтобы избежать P2003
    const user = await db.user.findUnique({ where: { id: authUser.userId } })
    if (!user) {
      await db.user.create({
        data: {
          id: authUser.userId,
          email: authUser.email,
          password: "", // пароль может быть не нужен при внешней аутентификации
          role: authUser.role || "user",
          name: (authUser as any).name ?? null,
        },
      })
    }

    const profile = await db.profile.upsert({
      where: { userId: authUser.userId },
      update: {},
      create: { userId: authUser.userId, balance: 0 },
    })

    return NextResponse.json(profile)
  } catch (error) {
    console.error("[v0] Error fetching profile:", error)
    return NextResponse.json({ error: "Failed to fetch profile" }, { status: 500 })
  }
}

export async function PATCH(request: Request) {
  try {
    const authUser = await getAuthUser()

    if (!authUser) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 })
    }

    const body = await request.json()

    // Обновляем существующий или создаём новый профиль для пользователя
    const profile = await db.profile.upsert({
      where: { userId: authUser.userId },
      update: body,
      create: { userId: authUser.userId, ...(body ?? {}) },
    })

    return NextResponse.json(profile)
  } catch (error) {
    console.error("[v0] Error updating profile:", error)
    return NextResponse.json({ error: "Failed to update profile" }, { status: 500 })
  }
}
